<template>
  <div class="container">
    <div class="eosWithdraw">
      <el-form :model="withdrawForm" ref="withdrawForm" label-width="58px" :rules="withdrawRules">
        <el-form-item :label="this.$t('assetScene.amount')" prop="amount">
          <div>
            <el-input
              :placeholder="this.$t('assetScene.withdrawamount')"
              v-model="withdrawForm.amount"
              class="input-with-select"
            >
          
              <el-select
                v-model="withdrawForm.coin_code"
                slot="append"
                placeholder="请选择"
                style="width:100px"
              >
                <el-option label="EOS" value="EOS"></el-option>
                <el-option label="ONES" value="ONES"></el-option>
              </el-select>
            </el-input>
          </div>
        </el-form-item>
        <el-form-item label-width="0px" align="center">
          <el-button
            type="primary"
            :loading="loading"
            @click="withdraw"
            style="width:50%;"
          >{{this.$t("assetScene.withdraw")}}</el-button>
        </el-form-item>
      </el-form>

      <div class="current">
        {{this.$t("assetScene.AccountBalance")}}
        <span>{{cur_coin_amount}} {{cur_coin_code}}</span>
      </div>

      <div class></div>
    </div>
  </div>
</template>

<script>
import AssetService from '../../../api/asset'
import Util from '../../../utils/util'

export default {
  data() {
    var validateAmount = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('assetScene.withdrawPlaceholder')))
      return callback()
    }
    return {
      cur_coin_code: 'EOS',
      cur_coin_amount: '0.00',

      balances: {
        usdt: 0,
        dt: 0,
        ones: 0
      },

      loading: false,
      withdrawForm: {
        amount: '',
        coin_code: 'EOS'
      },
      withdrawRules: {
        amount: [{ validator: validateAmount, trigger: 'blur' }]
      }
    }
  },
  watch: {
    'withdrawForm.coin_code': function(val) {
      console.log(val)
      var key = val.toLowerCase()
      this.cur_coin_code = val
      this.cur_coin_amount = Number(this.balances[key]).toFixed(4)
    }
  },


  mounted: function() {
    //action=deposit
    var coin_code = this.$route.params.coin_code
    var coin_amount = this.$route.params.coin_amount

    if (coin_code) {
      this.cur_coin_code = coin_code.toUpperCase()
      this.cur_coin_amount = Number(coin_amount).toFixed(4)
      this.withdrawForm.coin_code = coin_code.toUpperCase()
    }

    this.getBalances()
  },
    methods: {
    withdraw() {
      this.$refs.withdrawForm.validate(valid => {
        if (valid) {
          this.loading = true
          var amount = this.withdrawForm.amount
          var coin_code = this.withdrawForm.coin_code.toLowerCase()
          var params = {
            type: 'eos',
            amount: amount,
            coin_code: coin_code
          }
          AssetService.withdraw(params).then(res => {
            this.loading = false
            if (res.code == 0) {
              this.$message({
                message: this.$t('message.withdraw_success'),
                type: 'success',
                center: true
              })
              this.getBalances()
            } else {
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'error',
                center: true
              })
            }
          })
        } else {
          return false
        }
      })
    },
    getBalances() {
      AssetService.balances().then(res => {
        if (res.code == 0) {
          // this.balances = res.data.dragonex_balances
          for (var i in res.data.coins) {
            this.balances[i] = res.data.coins[i]["balances"]['eos']
              ? res.data.coins[i]["balances"]['eos']
              : '0.00'
            console.log(res.data.coins)
          }
          var key = this.cur_coin_code.toLowerCase()
          this.cur_coin_amount = Number(this.balances[key]).toFixed(4)
        }
      })
    }
  }
}
</script>

<style scoped>
.eosWithdraw {
  width: 96%;
  margin: 0 auto;
}

.container {
  width: 100%;
  /* height: 100vh; */
  margin-top: 45px;
  box-sizing: border-box;
  overflow: hidden;
}

.current {
  width: 100%;
  line-height: 7vh;
  margin-top: 0vh;
  font-size: 0.9rem;
  color: #888;
  text-align: right;
}
.current span {
  color: #ffc02d;
  font-size: 0.9rem;
  font-weight: 800;
}
</style>


